"use client";
import { useState, useMemo } from 'react';
import { useFinance } from '@/context/FinanceContext';
import TransactionForm from '@/components/forms/TransactionForm';
import ComplexFinanceChart from '@/components/dashboard/ComplexFinanceChart';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Dialog, DialogContent, DialogTrigger, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { PlusCircle, MinusCircle, TrendingDown, TrendingUp, PiggyBank, Wallet, Settings } from 'lucide-react';
import { isSameMonth, isSameYear, isSameDay, format } from 'date-fns';
import { es } from 'date-fns/locale';

// Imports necesarios al inicio de app/page.tsx
import Link from 'next/link';

export default function Dashboard() {
  const { transactions, categories, accounts } = useFinance(); // Traemos accounts AQUÍ, al inicio
  const [period, setPeriod] = useState('month');
  const [dateFilter, setDateFilter] = useState(new Date());

  const filteredTx = useMemo(() => {
    return transactions.filter(t => {
        const tDate = new Date(t.date);
        if (period === 'month') return isSameMonth(tDate, dateFilter);
        if (period === 'year') return isSameYear(tDate, dateFilter);
        if (period === 'day') return isSameDay(tDate, dateFilter);
        return true;
      }).sort((a,b) => new Date(b.date).getTime() - new Date(a.date).getTime());
  }, [transactions, period, dateFilter]);

  const incomeReal = filteredTx.filter(t => t.type === 'income').reduce((acc, curr) => acc + curr.amount, 0);
  const expenseReal = filteredTx.filter(t => t.type === 'expense').reduce((acc, curr) => acc + curr.amount, 0);
  const savingsReal = filteredTx.filter(t => t.type === 'income' && (t.categoryId === 'cat_sys_savings' || t.categoryId === 'cat_ahorro_meta'))
                                .reduce((acc, curr) => acc + curr.amount, 0);
  const remaining = incomeReal - expenseReal;
  const expensePct = incomeReal > 0 ? (expenseReal / incomeReal) * 100 : 0;
  const savingPct = incomeReal > 0 ? (savingsReal / incomeReal) * 100 : 0;

  return (
    <div className="space-y-6 pb-24">
      {/* Encabezado */}
      <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
        <div>
            <h1 className="text-3xl font-extrabold bg-clip-text text-transparent bg-gradient-to-r from-indigo-400 via-purple-400 to-pink-400 tracking-tight">
            HOME FINANCE
            </h1>
            <p className="text-sm text-zinc-400">{format(dateFilter, "EEEE, d 'de' MMMM yyyy", { locale: es })}</p>
        </div>
        <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
          <div className="flex items-center gap-4">
              <div>
                  <h1 className="text-3xl font-extrabold bg-clip-text text-transparent bg-gradient-to-r from-indigo-400 via-purple-400 to-pink-400 tracking-tight">
                  HOME FINANCE
                  </h1>
                  <p className="text-sm text-zinc-400">{format(dateFilter, "EEEE, d 'de' MMMM yyyy", { locale: es })}</p>
              </div>
              {/* BOTÓN DE CONFIGURACIÓN AGREGADO AQUÍ */}
              <Link href="/admin">
                <Button variant="outline" size="icon" className="border-zinc-700 bg-zinc-900 hover:bg-zinc-800 text-zinc-400 hover:text-white transition-colors">
                    <Settings className="h-5 w-5" />
                </Button>
              </Link>
          </div>
          
          <div className="flex gap-3 w-full md:w-auto">
              {/* ... Botones de Ingreso y Gasto siguen igual ... */}
          </div>
        </div>
        
        <div className="flex gap-3 w-full md:w-auto">
            <Dialog>
                <DialogTrigger asChild>
                    <Button className="flex-1 md:flex-none bg-emerald-600 hover:bg-emerald-700 text-white shadow-lg">
                        <PlusCircle className="mr-2 h-5 w-5" /> Ingreso
                    </Button>
                </DialogTrigger>
                <DialogContent className="bg-zinc-950 border-zinc-800 sm:max-w-[500px]">
                    <DialogTitle className="text-emerald-400">Registrar Nuevo Ingreso</DialogTitle>
                    <TransactionForm defaultType="income" />
                </DialogContent>
            </Dialog>

            <Dialog>
                <DialogTrigger asChild>
                    <Button className="flex-1 md:flex-none bg-rose-600 hover:bg-rose-700 text-white shadow-lg">
                        <MinusCircle className="mr-2 h-5 w-5" /> Gasto
                    </Button>
                </DialogTrigger>
                <DialogContent className="bg-zinc-950 border-zinc-800 sm:max-w-[500px]">
                    <DialogTitle className="text-rose-400">Registrar Nuevo Gasto</DialogTitle>
                    <TransactionForm defaultType="expense" />
                </DialogContent>
            </Dialog>
        </div>
      </div>

      {/* GRÁFICA AVANZADA */}
      <Card className="bg-zinc-900/50 border-zinc-800 shadow-xl relative overflow-hidden">
        <CardHeader className="pb-2">
            <CardTitle>Comportamiento Mensual</CardTitle>
            <CardDescription>Flujo de dinero día a día</CardDescription>
        </CardHeader>
        <CardContent>
           <ComplexFinanceChart transactions={transactions} period={period} currentDate={dateFilter} />
        </CardContent>
      </Card>

      <Tabs defaultValue="month" onValueChange={setPeriod} className="space-y-6">
        <TabsList className="bg-zinc-900 border border-zinc-800">
            <TabsTrigger value="day">Día</TabsTrigger>
            <TabsTrigger value="month">Mes</TabsTrigger>
            <TabsTrigger value="year">Año</TabsTrigger>
        </TabsList>

        <TabsContent value={period} className="space-y-6 animate-in fade-in slide-in-from-bottom-4">
          <div className="grid grid-cols-2 lg:grid-cols-4 gap-4">
            <KPICard title="Ingresos" value={incomeReal} icon={<TrendingUp className="text-emerald-400" />} colorClass="text-emerald-400" bgClass="bg-emerald-950/30" borderClass="border-emerald-900/50" />
            <KPICard title="Gastos" value={expenseReal} icon={<TrendingDown className="text-rose-400" />} colorClass="text-rose-400" subtext={`${expensePct.toFixed(1)}%`} bgClass="bg-rose-950/30" borderClass="border-rose-900/50"/>
            <KPICard title="Ahorro" value={savingsReal} icon={<PiggyBank className="text-blue-400" />} colorClass="text-blue-400" subtext={`${savingPct.toFixed(1)}%`} bgClass="bg-blue-950/30" borderClass="border-blue-900/50"/>
            <KPICard title="Restante" value={remaining} icon={<Wallet className="text-purple-400" />} colorClass={remaining >= 0 ? "text-white" : "text-red-500"} bgClass="bg-purple-950/30" borderClass="border-purple-900/50"/>
          </div>

          <Card className="bg-zinc-900 border-zinc-800 shadow-lg">
            <CardHeader><CardTitle>Detalle de Movimientos</CardTitle></CardHeader>
            <CardContent className="p-0">
                  <div className="divide-y divide-zinc-800 text-sm">
                    {filteredTx.length === 0 ? (
                      <p className="text-zinc-500 text-center py-8">No hay movimientos.</p>
                    ) : (
                      filteredTx.map(tx => {
                        const category = categories.find(c => c.id === tx.categoryId);
                        // CORRECCIÓN: Usamos 'accounts' que trajimos al inicio, no llamamos el hook aquí
                        const account = accounts.find(a => a.id === tx.accountId); 
                        
                        return (
                        <div key={tx.id} className="flex flex-col md:flex-row justify-between items-start md:items-center p-4 hover:bg-zinc-800/50 gap-2">
                          <div className="flex gap-3 items-center flex-1">
                            <div className={`h-10 w-1 rounded-full ${tx.type === 'income' ? 'bg-emerald-500' : 'bg-rose-500'}`}></div>
                            <div>
                              <p className="font-bold text-zinc-200">{tx.description || category?.name}</p>
                              <div className="flex flex-wrap gap-x-2 text-xs text-zinc-400">
                                <span>{format(new Date(tx.date), 'dd/MM')}</span> | 
                                <span className="text-indigo-300">{category?.name}</span> |
                                <span>{account?.name}</span>
                              </div>
                            </div>
                          </div>
                          <span className={`text-lg font-bold font-mono ${tx.type === 'income' ? 'text-emerald-400' : 'text-rose-400'}`}>
                              {tx.type === 'income' ? '+' : '-'} ${tx.amount.toLocaleString()}
                          </span>
                        </div>
                      )})
                    )}
                  </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  );
}

function KPICard({ title, value, icon, colorClass, subtext, bgClass, borderClass }: any) {
  return (
    <Card className={`backdrop-blur-sm ${bgClass} border ${borderClass} shadow-lg relative overflow-hidden`}>
      <CardContent className="p-5 flex flex-col justify-between h-full relative z-10">
        <div className="flex justify-between items-center mb-3">
          <span className="text-zinc-300 font-medium text-sm uppercase">{title}</span>
          <div className={`p-2 rounded-lg bg-black/20 ${colorClass}`}>{icon}</div>
        </div>
        <div>
          <span className={`text-2xl lg:text-3xl font-extrabold tracking-tight font-mono ${colorClass}`}>
            ${value.toLocaleString()}
          </span>
          {subtext && <p className="text-xs font-medium text-zinc-400 mt-2">{subtext}</p>}
        </div>
      </CardContent>
    </Card>
  );
}